import Vue from 'vue'
import Router from 'vue-router'
import Landing from '@/views/landing'//进入页面
import Index from '@/views/Index'//index公用页面
import News from '@/views/News'//资讯页面
import Players from '@/views/Players'//玩家页面
import Heroes from '@/views/Heroes'//英雄页面
import Video from '@/views/Video'//视频页面
import Public from '@/views/publicHeroes'//英雄页面跳转到英雄详情页面
import searchild from '@/views/searchild'//玩家页面点击之后进入页面
import wardetails from '@/views/wardetails'//玩家页面点击之后进入页面之后在进入的对战详情页面

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        component: Landing
    }, {
        path: '/Index',
        component: Index,
        children: [//index子页面
            { path: 'news', component: News },
            { path: 'players', component: Players },
            { path: 'heroes', component: Heroes },
            { path: 'video', component: Video },
            { path: 'hero/:id', component: Public, name: 'hero' },
            //段位、级别、用户id、大区id
            { path: 'searchild/:tier/:queue/:qquin/:areaid', component: searchild, name: 'searchild' },
            //级别、大区id、游戏id
            { path: 'wardetails/:qquin/:areaid/:gameid', component: wardetails, name: 'wardetails' }
        ]
    }]
})



