<template>
    <div>
        <!-- //重写头部 -->
        <div class="header" @click="goinfo()">
            <div class="return"></div>
            <p class="header_title">对战详情</p>
        </div>
        <div class="detailtitle">
            <span class="timetadil">开始时间 :{{start_time(startime)}}</span>
            <span class="timetadil">总时长 ：{{title(titletime)}}分钟</span>
            <span class="timetadil">模式 ：{{gametype|gametype}}</span>
        </div>
        <!-- //获胜方固定div -->
        <div class="victorydiv">
            <div class="vdiv">
                <p>获胜方</p>
            </div>
            <img src="../images/img/victory.png">
        </div>
        <div class="victory" v-for="(vict,index) in victorys" v-if="vict.win==1">
            <div class="touxiang"><img :src="vict.champion_id|headUrlHeroes"></div>
            <div class="grade">
                <p>LV{{vict.level}}</p>
            </div>
            <p class="vname">{{vict.name}}</p>
            <div class="equipdiv">
                <div><img :src="'http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/'+vict.item0+'.png'"></div>
                <div><img :src="'http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/'+vict.item1+'.png'"></div>
                <div><img :src="'http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/'+vict.item2+'.png'"></div>
                <div><img :src="'http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/'+vict.item3+'.png'"></div>
                <div><img :src="'http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/'+vict.item4+'.png'"></div>
                <div><img :src="'http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/'+vict.item5+'.png'"></div>
            </div>
            <span class="zhanji">{{vict.champions_killed}}/{{vict.num_deaths}}/{{vict.assists}}</span>
            <div class="xiahuaxian"></div>
        </div>
        <!-- //失败方固定div -->
        <div class="fenge"></div>
        <div class="victorydiv">
            <div class="vdiv">
                <p>失败方</p>
            </div>
            <img src="../images/img/victory.png">
        </div>
        <div class="victory" v-for="(vict,index) in victorys" v-if="vict.win==2">
            <div class="touxiang"><img :src="vict.champion_id|headUrlHeroes"></div>
            <div class="grade">
                <p>LV{{vict.level}}</p>
            </div>
            <p class="vname">{{vict.name}}</p>
            <div class="equipdiv">
                <div><img :src="'http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/'+vict.item0+'.png'"></div>
                <div><img :src="'http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/'+vict.item1+'.png'"></div>
                <div><img :src="'http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/'+vict.item2+'.png'"></div>
                <div><img :src="'http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/'+vict.item3+'.png'"></div>
                <div><img :src="'http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/'+vict.item4+'.png'"></div>
                <div><img :src="'http://ddragon.leagueoflegends.com/cdn/6.21.1/img/item/'+vict.item5+'.png'"></div>
            </div>
            <span class="zhanji">{{vict.champions_killed}}/{{vict.num_deaths}}/{{vict.assists}}</span>
            <div class="xiahuaxian"></div>
        </div>
    </div>
</template>
<script>
import {
    mapActions
} from "vuex"
import Vue from 'vue'
import axios from 'axios'
import {
    getAreaName,
    headUrl,
    gametype,
    headUrlHeroes
} from '../filters'
axios.defaults.headers.common = {
    'DAIWAN-API-TOKEN': '32133-23493-43483-4A83A'
}
export default {
    data() {
            return {
                data: [],
                gameid: this.$route.params.gameid, //返回游戏唯一id
                qquin: this.$route.params.qquin, //返回用户唯一id
                areaids: this.$route.params.areaid, //返回大区id
                vict: "", //返回战绩数据
                victorys: "", //返回英雄对战单场个人信息
                startime: "", //返回开始时间
                titletime: "", //返回总时间
                gametype: "" //返回单场类型
            }
        },
        created() {
            this.$parent.show = false; //隐藏底部
            this.ready();
            //this.$parent.toubu = false;
        },
        methods: {
            goinfo() {
                this.$router.history.go(-1);
            },
            ready() {
                axios.get('/api/GameDetail?qquin=' + this.qquin + '&vaid=' + this.areaids + '&gameid=' + this.gameid).then(res => {
                    //console.log(res.data.data[0]);
                    //英雄对战单场个人信息
                    this.victorys = res.data.data[0].battle.gamer_records;
                    //console.log(res.data.data[0].battle.gamer_records);
                    //比赛开始时间
                    this.startime = res.data.data[0].battle.start_time;
                    //单场比赛总时间
                    this.titletime = res.data.data[0].battle.duration;
                    //比赛类型
                    this.gametype = res.data.data[0].battle.game_type
                }, err => {
                    console.log(err);
                })
            },
            //对开始时间进行截取
            start_time(startime) {
                return startime.substr(startime.indexOf(" "))
            },
            //对总时间进行换算（分钟）
            title(titletime) {
                return (titletime / 60).toFixed()
            }
        },
        filters: {
            getAreaName,
            headUrl,
            gametype,
            headUrlHeroes
        }
}
</script>
<style lang="less">
.detailtitle {
    width: 100%;
    height: .8rem;
    background-color: #f0f0f0;
    position: relative;
    span {
        height: .8rem;
        display: block;
        float: left;
        color: #3b3b3b;
        font-size: .26rem;
        font-weight: bold;
        margin-left: .16rem;
        padding-left: .2rem;
        line-height: .8rem;
    }
}

.victorydiv {
    width: 100%;
    height: .93rem;
    border-bottom: .01rem solid #e1e1e1;
    position: relative;
    .vdiv {
        position: absolute;
        top: .18rem;
        left: .18rem;
        width: 1.44rem;
        height: .55rem;
        background-color: #ff9900;
        p {
            line-height: .55rem;
            font-size: .27rem;
            color: #fffefd;
            font-weight: bold;
            text-align: center;
        }
    }
    img {
        width: 1.59rem;
        height: .39rem;
        position: absolute;
        top: .29rem;
        right: .33rem;
    }
}

.victory {
    width: 100%;
    height: 1.9rem;
    position: relative;
    overflow: hidden;
    .touxiang {
        width: 1.25rem;
        height: 1.26rem;
        background-image: url("../images/img/10.jpg");
        background-repeat: no-repeat;
        background-size: 100%;
        position: absolute;
        top: .09rem;
        left: .19rem;
        img {
            position: absolute;
            width: 1.25rem;
            height: 1.26rem;
            top: 0;
            left: 0;
        }
    }
    .vname {
        color: #010101;
        font-size: .28rem;
        font-weight: bold;
        margin-left: 1.76rem;
        margin-top: .26rem;
    }
    .grade {
        position: absolute;
        width: 1.25rem;
        height: .55rem;
        top: 1.35rem;
        left: .19rem;
        border: .01rem solid #24a723;
        box-sizing: border-box;
        p {
            color: #000000;
            font-size: .24rem;
            font-weight: bold;
            text-align: center;
            line-height: .55rem;
        }
    }
    .equipdiv {
        margin-top: .48rem;
        margin-left: 1.61rem;
        position: relative;
        width: 4.08rem;
        height: .5rem;
        overflow: hidden;
        div {
            position: relative;
            width: .5rem;
            height: .5rem;
            margin-left: .18rem;
            padding-bottom: .56rem;
            background-image: url("../images/img/1.png");
            background-repeat: no-repeat;
            background-size: 100%;
            float: left;
            img {
                width: .5rem;
                height: .5rem;
                position: absolute;
                left: 0;
                top: 0;
            }
        }
    }
    .xiahuaxian {
        width: 5.82rem;
        height: .01rem;
        position: absolute;
        background-color: #e1e1e1;
        bottom: 0;
        right: 0;
    }
    .zhanji {
        position: absolute;
        right: .36rem;
        top: .83rem;
        font-size: .2rem;
        color: #a4a4a4;
    }
}

.fenge {
    width: 100%;
    height: .02rem;
    background-color: #e1e1e1;
}
</style>



