<template>
    <div>
        <!-- 加载轮播动画 -->
        <div style="height:3.75rem;">
            <swiper :options="swiperOption">
                <swiper-slide v-for="item in bannerNews">
                    <a :href="item.article_url">
                        <img width="100%" height="27.8%" :src="item.image_url_big">
                    </a>
                </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
        </div>
        <!-- 资讯列表 -->
        <div>
            <div class="newsDiv" v-for="item in newsList">
                <a :href='item.article_url'>
                    <img v-lazy="item.image_url_small">
                    <p class="lineOne">{{item.title}}</p>
                    <p class="lineTwo">{{item.summary}}</p>
                    <p class="lineThree"><span class="lineY">{{item.publication_date.substr(0,10)}}</span><span class="lineX">{{getNumber(parseInt(item.pv))}}</span></p>
                </a>
            </div>
        </div>
    </div>
</template>
<script>
//使用轮播插件，用命令提示符进行安装
import {
    swiper,
    swiperSlide
} from "vue-awesome-swiper"
//导入bannerNews。js
import {
    bannerNews
} from "../data/banner"
//导入newslist.js
import {
    newsList
} from "../data/newslist"
import {
    mapActions
} from "vuex"
export default {
    data() {
            return {
                //设置轮播属性状态
                swiperOption: {
                    autoplay: 3000,
                    pagination: ".swiper-pagination",
                    paginationClickable: true,
                    effect: 'fade',
                    //spaceBetween: 300,
                    centeredSlides: true,
                    noSwiping: true,
                    autoplayDisableOnInteraction: false
                },
                bannerNews,
                newsList
            }
        },
        methods: {
            //判断评阅的人数
            getNumber: function(num) {
                if (num > 10000) {
                    return (num / 10000).toFixed() + "万阅";
                } else {
                    return num + "阅";
                }
            },
            ...mapActions(["setTitle", "setCur"])
        },
        //在components里面调用组件，swiper、swiperSlide标签
        components: {
            swiper,
            swiperSlide
        },
        created() {
            //this.$parent.headerTitle="资讯"
            this.setTitle("资讯"); //改变头部名称
            this.$parent.toubu = true; //头部显示
            this.setCur(0); //资讯底部改变蓝色
        }
}
</script>
<style lang="less">
.swiper-pagination span {
    width: .14rem;
    height: .14rem;
    border-radius: 50%;
    opacity: 1;
}

.swiper-pagination .swiper-pagination-bullet {
    background-color: #d9d9d9;
}

.swiper-pagination .swiper-pagination-bullet-active {
    background-color: #41a5fb;
}

.newsDiv {
    width: 100%;
    height: 1.5rem;
    border-bottom: .02rem solid #f0f0f0;
    position: relative;
    img{
        width: 1.5rem;
        height: 1.3rem;
        position: absolute;
        left: .11rem;
        top: .1rem;
    }
    .lineOne {
        color: #323232;
        font-size: .28rem;
        position: absolute;
        left: 1.82rem;
        top: .14rem;
        font-weight: bold;
    }
    .lineTwo {
        width: 5.6rem;
        color: #323232;
        font-size: .25rem;
        position: absolute;
        left: 1.82rem;
        top: .61rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    .lineThree {
        color: #323232;
        font-size: .23rem;
        position: absolute;
        left: 1.82rem;
        top: 1.03rem;
        .lineX {
            color: #41a7fc;
            margin-left: .51rem;
        }
    }
}
</style>



