<template>
    <div>
        <div class="header">
            <div class="return" @click="go()"></div>
            <p class="header_title">{{heroesCode.name}}</p>
        </div>
        <div class="p_img">
            <img :src="'http://cdn.tgp.qq.com/pallas/images/skins/original/'+id+'-000.jpg'">
            <div class="text">
                <span>{{heroesCode.name}}   </span>
                <span>{{heroesCode.tags|htype}}</span>
                <span>攻：{{att.attack}}  </span>
                <span>法：{{att.defense}}  </span>
                <span>防：{{att.difficulty}}  </span>
                <span>操作：{{att.magic}}  </span>
            </div>
        </div>
        <div class="border change_page">
            <div class="active explain" :class="{'enjoy':flag==0}" id="0" @click="light($event)">
                <p>技能说明</p>
                <div></div>
            </div>
            <div class="active explain" :class="{'enjoy':flag==1}" id="1" @click="light($event)">
                <p>皮肤欣赏</p>
                <div></div>
            </div>
        </div>
        <div id="tab1" v-show="onepage">
            <div class="jn_jieshao">
                <img src="../images/img/jineng.png">
                <p>技能介绍</p>
            </div>
            <div class="skill">
                <img :src="'http://ossweb-img.qq.com/images/lol/img/passive/'+imgdat.full">
                <p class="skillname">{{passive.name}}</p>
                <p class="skillexplain">{{passive.description}}</p>
            </div>
            <div class="skill" v-for="skills in heroesCode.spells">
                <img :src="'http://ossweb-img.qq.com/images/lol/img/spell/'+skills.image.full">
                <p class="skillname">{{skills.name}}</p>
                <p class="skillexplain" v-html="skills.tooltip">{{skills.tooltip}}</p>
            </div>
            <div class="jn_jieshao">
                <img src="../images/img/jineng.png">
                <p>技能加点推荐</p>
            </div>
            <div class="points">
                <p>{{trait|getBatleType}}</p>
            </div>
        </div>
        <div id="tab2" v-show="twopage">
            <div class="jn_jieshao">
                <img src="../images/img/jineng.png">
                <p>点击皮肤可以观看介绍视频哦</p>
            </div>
            <div class="skil" v-for="herosource in heroesCode.skins">
                <a :href="herosource.source">
                    <img :src="'http://cdn.tgp.qq.com/pallas/images/skins/original/'+id+'-'+herosource.id+'.jpg'">
                    <div class="skiltitle">
                        <p>{{herosource.name}}</p>
                    </div>
                </a>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import {
    mapActions
} from "vuex"
import Vue from 'vue'
import {
    //导入过滤方法
    htype,
    getBatleType
} from '../filters'
axios.defaults.headers.common = {
    'DAIWAN-API-TOKEN': '32133-23493-43483-4A83A'
}
export default {
    data() {
            return {
                //定义flag的初始值，进来页面显示技能说明样式
                flag: 0,
                //得到路由里的英雄id
                id: this.$route.params.id,
                heroesCode: [],
                //定义onepage的初始值，进来页面显示技能说明页面
                onepage: true,
                //定义twopage的初始值，进来页面显示皮肤欣赏页面
                twopage: false,
                att: [],
                imgdat: "",
                passive: "",
                trait: ""
            }
        },
        created() {
            //this.setTitle("英雄列表");
            this.$parent.show = false; //隐藏底部
            this.ready();
            //隐藏头部
            this.$parent.toubu = false;
        },
        methods: {
            //...mapActions(["setTitle"]),
            //执行页面切换卡函数
            light: function(e) {
                //通过event.currentTarget得到元素里的id，赋值给flag
                this.flag = e.currentTarget.id;
                //if判断id是否等于0
                //onepage显示（技能），twopage(皮肤欣赏)隐藏
                if (e.currentTarget.id == 0) {
                    this.onepage = true
                    this.twopage = false
                } else if (e.currentTarget.id == 1) {
                    //onepage隐藏（技能），twopage(皮肤欣赏)显示
                    this.onepage = false
                    this.twopage = true
                }
            },
            ready() {
                //英雄id给axios，解析英雄详情数据
                axios.get('/api/GetChampionDetail?champion_id=' + this.$route.params.id).then(res => {
                    //console.log(this.$route.params.id);
                    //console.log('英雄详情数据', res);
                    //console.log(res.data.data[0].trait);
                    var dat = res.data.data[0];
                    var imgdat = dat.passive.image
                    this.heroesCode = dat;
                    this.passive = dat.passive;
                    this.att = dat.info;
                    this.imgdat = imgdat;
                    this.trait = dat.trait;
                }, err => {
                    console.log(err);
                })
            },
            go() {
                //跳转到英雄页面
                this.$router.history.go(-1);
            }
        },
        components: {

        },
        filters: {
            //过滤
            htype,
            getBatleType
        }
}
</script>
<style lang="less">
.p_img {
    width: 100%;
    height: 3.71rem;
    position: relative;
    img {
        width: 100%;
        height: 3.71rem;
    }
    .text {
        width: 100%;
        height: .8rem;
        position: absolute;
        bottom: 0;
        left: 0;
        background: rgba(0, 0, 0, .3);
        font-size: .3rem;
        color: #fff;
        line-height: .8rem;
        padding-left: .45rem;
        span {
            font-size: .3rem;
            color: #fff;
            line-height: .8rem;
        }
    }
}

.change_page {
    width: 100%;
    height: .83rem;
    .explain {
        width: 3.75rem;
        height: .83rem;
        float: left;
        position: relative;
        p {
            font-size: .26rem;
            color: #181818;
            font-weight: bold;
            position: absolute;
            top: .28rem;
            left: 1.45rem
        }
        div {
            width: 2.27rem;
            height: .05rem;
            background-color: #ffffff;
            position: absolute;
            left: .95rem;
            bottom: 0;
        }
    }
    .enjoy {
        width: 3.75rem;
        height: .83rem;
        float: left;
        position: relative;
        p {
            font-size: .26rem;
            color: #40a6fb;
            font-weight: bold;
            position: absolute;
            top: .28rem;
            left: 1.45rem
        }
        div {
            width: 2.27rem;
            height: .05rem;
            background-color: #40a6fb;
            position: absolute;
            left: .95rem;
            bottom: 0;
        }
    }
}

.jn_jieshao {
    width: 100%;
    height: .86rem;
    background-color: #f0f0f0;
    position: relative;
    img {
        width: .38rem;
        height: .38rem;
        position: absolute;
        left: .13rem;
        top: .24rem;
    }
    p {
        color: #343434;
        position: absolute;
        font-size: .28rem;
        font-weight: bold;
        position: absolute;
        left: .67rem;
        top: .3rem;
    }
}

.skill {
    width: 100%;
    min-height: 2.58rem;
    border-bottom: .01rem solid #f0f0f0;
    position: relative;
    img {
        width: 1.4rem;
        height: 1.4rem;
        padding-top: .75rem;
        padding-left: .2rem;
    }
    .skillname {
        font-size: .28rem;
        color: #333333;
        font-weight: bold;
        position: absolute;
        top: .14rem;
        left: 1.83rem;
    }
    .skillexplain {
        width: 5.54rem;
        font-size: .28rem;
        color: #333333;
        margin-left: 1.83rem;
        margin-top: -1.76rem;
    }
}

.points {
    width: 100%;
    position: relative;
    p {
        font-size: .27rem;
        color: #a4a4a4;
        position: absolute;
        top: .2rem;
        left: .32rem;
        font-weight: normal;
        letter-spacing: .05rem;
        display: block;
        width: 5.9rem;
        padding-bottom: 0.25rem;
    }
}

.skil {
    width: 100%;
    text-align: center;
    img {
        width: 7.23rem;
        height: 3.53rem;
        display: block;
        margin: 0 auto;
    }
    .skiltitle {
        width: 7.23rem;
        height: .83rem;
        margin: 0 auto 0;
        line-height: .83rem;
        p {
            color: #333333;
            font-size: .28rem;
            font-weight: bold;
        }
    }
}
</style>



