<template>
    <div>
        <!-- //重写头部 -->
        <div class="headers">
            <div class="return" @click="goinfo()"></div>
            <p class="header_title">搜索</p>
        </div>
        <scroller style="position:fixed;top:.8rem;" :on-refresh="refresh" ref="my_scroller">
            <div class="matches">
                <!-- 英雄信息-->
                <div class="info">
                    <img :src="player.icon|headUrl">
                    <p class="dengji">{{player.level}}级</p>
                    <p class="pername">{{areaids|getAreaName}}</p>
                    <p class="duanwei">{{grading}}</p>
                    <p class="zhanli">战斗力 ：{{player.power_value}}</p>
                    <p class="zan">赞 : {{player.praise_num}}</p>
                </div>
                <div class="record">
                    <ul>
                        <li>
                            <div>
                                <img src="../images/img/Sumdetails-2.png">
                                <p>三杀 ：{{data1.triple_kills}}</p>
                            </div>
                            <div>
                                <img src="../images/img/Sumdetails-3.png">
                                <p>四杀 ：{{data1.quadra_kills}}</p>
                            </div>
                            <div>
                                <img src="../images/img/Sumdetails-4.png">
                                <p>五杀 ：{{data1.penta_kills}}</p>
                            </div>
                            <div>
                                <img src="../images/img/Sumdetails-5.png">
                                <p>总击杀 ：{{data1.kills_total}}</p>
                            </div>
                            <div>
                                <img src="../images/img/Sumdetails-6.png">
                                <p>MVP ：{{data2.total_match_mvps+data2.total_rank_mvps}}</p>
                            </div>
                            <div>
                                <img src="../images/img/Sumdetails-7.png">
                                <p>超神 ：{{data1.god_like_num}}</p>
                            </div>
                        </li>
                    </ul>
                </div>
                <!-- 加载英雄对战场次 -->
                <div class="enterinfo" v-for="war in wars" @click="eninfo(war)">
                    <img class="imginfo" :src="'http://cdn.tgp.qq.com/pallas/images/champions_id/' +war.champion_id+'.png'">
                    <p class="infoname" v-html="success(war.win)">{{success(war.win)}}</p>
                    <p class="typegame">{{war.game_type|gametype}}</p>
                    <img class="enimg" src="../images/img/u379.png">
                    <div class="xiahuaxian"></div>
                </div>
                <pacman-loader :loading="loading" :color="color" :size="size"></pacman-loader>
            </div>
        </scroller>
    </div>
</template>
<script>
//vue-spinner加载中动画插件
import PacmanLoader from 'vue-spinner/src/PacmanLoader.vue'
import {
    mapActions
} from "vuex"
import Vue from 'vue'
import axios from 'axios'
import {
    getAreaName,
    headUrl,
    gametype
} from '../filters'
//导入下拉加载的插件
import Scroller from 'vue-scroller'
axios.defaults.headers.common = {
    'DAIWAN-API-TOKEN': '32133-23493-43483-4A83A'
}
export default {
    data() {
            return {
                data: [],
                queues: this.$route.params.queue,
                tiers: this.$route.params.tier,
                qquins: this.$route.params.qquin,
                areaids: this.$route.params.areaid,
                grading: "",
                player: "",
                data1: "",
                data2: "",
                wars: [],
                //控制是否可以加载更多
                flag: true,
                //刷新的页数，初始化
                page: 0,
                //对加载动画插件各属性进行初始化
                loading: true,
                color: "#5dc596",
                size: ".4rem"
            }
        },
        created() {
            //this.setTitle("英雄列表");
            this.$parent.show = false; //隐藏底部
            this.ready();
            this.$parent.toubu = false;
            this.getBattleInfo();
        },
        methods: {
            //...mapActions(["setTitle"]),
            goinfo() {
                this.$router.history.go(-1);
            },
            ready() {
                // this.ajax.get('/api/UserHotInfo?qquin=' + this.$route.params.qquin + '&vaid=' + this.$route.params.vaid).then(res => { // console.log(this.$route.params.qquin); // console.log('英雄详情数据', res); // this.data = res.data; // }, err => { // console.log(err); // })
                axios.get('/api/GetTierQueue?tier=' + this.tiers + '&queue=' + this.queues).then(res => {
                        this.heroes = res.data.data;
                        //console.log(res.data.data);
                        var data = res.data.data[0]
                        this.grading = data.return
                    }, err => {
                        console.log(err);
                    }),
                    axios.get('/api/UserHotInfo?qquin=' + this.qquins + '&vaid=' + this.areaids).then(res => {
                        //console.log(res.data.data[0])
                        this.player = res.data.data[0]
                    }, err => {
                        console.log(err);
                    })
                axios.get('/api/UserExtInfo?qquin=' + this.qquins + '&vaid=' + this.areaids).then(res => {
                    //console.log(res)
                    this.data1 = res.data.data[1]
                    this.data2 = res.data.data[2]
                }, err => {
                    console.log(err);
                })
            },
            //滚动刷新axios
            getBattleInfo: function() {
                axios.get('/api/CombatList?qquin=' + this.qquins + '&vaid=' + this.areaids + '&p=' + this.page).then(res => {
                    //console.log(res.data.data[0])
                    this.wars = this.wars.concat(res.data.data[0].battle_list);
                    //成功加载隐藏加载动画效果
                    this.loading = false;
                }, err => {
                    console.log(err);
                    //成功加载隐藏加载动画效果
                    this.loading = false;
                })
            },
            handler: function(el) {
                //获取scrollTop值
                var scrollTop = parseInt(el.scrollTop);
                //获取包含对象的高度
                var scrollHeight = el.scrollHeight;
                //offsetHeight = clientHeight + 滚动条 + 边框
                //clientHeight可视区域高度
                var offsetHeight = el.offsetHeight;
                if (scrollTop + 15 >= scrollHeight - offsetHeight) {
                    this.page++;
                    this.flag = false;
                    //page++加载动画效果
                    this.loading = true;
                }
            },
            success(num) {
                if (num == 1) {
                    return "<p style='color:#41d21f;'>胜利</p>"
                } else {
                    return "失败"
                }
            },
            eninfo(war) {
                this.$router.push({
                    name: 'wardetails',
                    params: {
                        gameid: war.game_id,
                        qquin: this.qquins,
                        areaid: this.areaids
                    }
                })
            },
            refresh() {
                this.page = 0;
                this.loading = false;
                this.wars = [];
                //this.$parent.show = false; //隐藏底部
                this.ready();
                this.getBattleInfo();
                this.top = this.top - 10;
                if (this.$refs.my_scroller)
                    this.$refs.my_scroller.finishPullToRefresh();
            }
        },
        watch: { //监测page改变调用ajax
            "page": function() {
                this.getBattleInfo()
            }
        },
        mounted() {
            var el = document.querySelector('.matches');
            //给列表添加滚动监听
            el.addEventListener('scroll', (function() {
                    //第一次加载 flag为TRUE，执行
                    if (this.flag) {
                        //flag在handler（）变为FALSE，但执行定时器的时候200之后又变为TRUE
                        this.handler(el);
                        let handleFlag = setTimeout((function() {
                            this.flag = true
                            clearTimeout(handleFlag)
                        }).bind(this), 200);
                    }
                }).bind(this)) //bind（this）指向vue整体，向监听里面可以取到flag值
        },
        components: {
            PacmanLoader,
            Scroller
        },
        filters: {
            getAreaName,
            headUrl,
            gametype
        }
}
</script>
<style lang="less">
.headers {
    width: 100%;
    height: .8rem;
    background-image: url("../images/img/header.png");
    position: fixed;
    top: 0;
    z-index: 999;
    .return {
        position: absolute;
        width: .18rem;
        height: .34rem;
        left: .33rem;
        top: .26rem;
        background-image: url("../images/img/u31.png");
        background-size: cover;
        background-repeat: no-repeat;
    }
    .header_title {
        color: #ffffff;
        font-size: .34rem;
        text-align: center;
        line-height: .8rem;
    }
}

.info {
    width: 100%;
    height: 3.56rem;
    background-image: url("../images/img/Sumdetails-1.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    text-align: center;
    position: relative;
    img {
        width: 1.28rem;
        height: 1.28rem;
        position: absolute;
        left: 3.11rem;
        top: .47rem;
        border-radius: 50%;
    }
    .dengji {
        font-weight: bold;
        font-size: .29rem;
        color: #fffeff;
        width: .65rem;
        position: absolute;
        left: 3.4rem;
        top: 1.93rem;
    }
    .pername {
        position: absolute;
        right: 3.73rem;
        top: 2.54rem;
        color: #fffeff;
        font-size: .23rem;
    }
    .duanwei {
        position: absolute;
        left: 4.07rem;
        top: 2.54rem;
        color: #fffeff;
        font-size: .23rem;
    }
    .zhanli {
        position: absolute;
        right: 3.57rem;
        top: 2.95rem;
        color: #fffeff;
        font-size: .23rem;
    }
    .zan {
        position: absolute;
        left: 4.09rem;
        top: 2.95rem;
        color: #fffeff;
        font-size: .23rem;
    }
}

.record {
    width: 100%;
    height: 3.82rem;
    border-bottom: 1px solid #e1e1e1;
    ul {
        li {
            list-style: none;
            div {
                float: left;
                height: 1.91rem;
                width: 2.5rem;
                position: relative;
                img {
                    width: .66rem;
                    height: .66rem;
                    position: absolute;
                    top: .44rem;
                    left: 1rem;
                }
                p {
                    position: absolute;
                    top: 1.39rem;
                    left: .7rem;
                    font-size: .22rem;
                    color: #333333;
                }
            }
        }
    }
}

.matches {
    overflow-y: auto;
    height: 12.56rem;
}

.enterinfo {
    width: 100%;
    height: 1.51rem;
    position: relative;
    .imginfo {
        width: 1rem;
        height: 1rem;
        position: absolute;
        border-radius: 50%;
        top: .33rem;
        left: .41rem;
    }
    .infoname {
        position: absolute;
        top: .43rem;
        left: 1.76rem;
        font-size: .29rem;
        font-weight: bold;
        color: #fe0000;
    }
    .typegame {
        position: absolute;
        top: .97rem;
        left: 1.76rem;
        font-size: .23rem;
        font-weight: bold;
        color: #666666;
    }
    .enimg {
        position: absolute;
        top: .72rem;
        right: .41rem;
        width: .13rem;
        height: .23rem;
    }
    .xiahuaxian {
        width: 7rem;
        height: .02rem;
        position: absolute;
        background-color: #e1e1e1;
        bottom: 0;
        right: 0;
    }
}

.loading {
    width: 100%;
    height: .5rem;
    display: block;
    font-size: .28rem;
    color: #000000;
    text-align: center;
    line-height: .5rem;
}
</style>



