import Vue from "vue"
import Vuex from "vuex"
import axios from 'axios'
Vue.use(Vuex)
//请求header参数、默认值
axios.defaults.headers.common = {
    'DAIWAN-API-TOKEN': '32133-23493-43483-4A83A'
}
export default new Vuex.Store({
    //状态集合
    state: {
        //头部默认值
        title: "资讯",
        //定义heroes的数组
        championList: [],
        //生命isCur的初始值
        isCur:0
    },
    //突变
    mutations: {
        //改变头部传值
        SET_TITLE: (state, title) => { state.title = title },
        //把list传入数组中
        SETCHAMPIONLIST: (state, list) => { state.championList = list },
        SET_CUR: (state, isCur) => { state.isCur = isCur },
    },
    //方法集合
    actions: {
        //调用vuejs自带的commit方法，把title传入commit
        setTitle: ({ commit }, title) => {
            commit("SET_TITLE", title)
        },
        //定义heroes的解析地址方法
        setChampionList:({commit})=>{         
            axios.get("/api/champion").then(res=>{
                //console.log('res====',res);
                commit("SETCHAMPIONLIST",res.data.data)
            },err=>{
                console.log(err);
            })
        },
        setCur: ({ commit }, isCur) => {
            commit("SET_CUR", isCur)
        }
    },
    //获取状态
    getters: {
        //获取头部，并可以进行渲染
        msg: state => state.title,
        championList:state=>state.championList,
        //获取isCUE的值
        isCur:state=>state.isCur
    }
})



