<template>
    <div>
        <!-- 搜索框 -->
        <div class="search">
            <input type=text placeholder="搜索召唤师" style="border-style:none" v-model="name">
            <div @click="jump" class="search_logo"></div>
            <p @click="jump" class="search_font">搜索</p>
        </div>
        <!-- 空白数据时加载 -->
        <div class="nothing" v-if="show">
            <img src="../images/img/noimg.png">
        </div>
        <!-- 加载搜索选中的英雄 -->
        <div class="playDiv" v-for="player in players" @click="clicPlay(player)">
            <img :src="player.icon_id|headUrl">
            <p class="pname">{{player.name}}</p>
            <p class="area">{{player.area_id|getAreaName}}</p>
            <p class="grade">{{player.level}}</p>
        </div>
    </div>
</template>
<script>
//导入过滤
import {
    getAreaName,
    headUrl
} from '../filters'
import {
    mapActions
} from "vuex"
export default {
    data() {
            return {
                //返回input框里写入的名字
                name: '',
                players: [],
                show: true,
                player: ''
            }
        },
        methods: {
            jump() {
                //点击搜索按钮执行函数，把name加入到ajax当中
                this.ajax.get('/api/UserArea?keyword=' + this.name).then(res => {
                    //console.log(res.data)
                    this.players = res.data.data;
                    //判断有没有内容，没有隐藏，有显示
                    if (res.data.code == 0) {
                        this.show = false
                    } else {
                        this.show = true
                    }
                }, err => {
                    console.log(err);
                })
            },
            ...mapActions(["setTitle","setCur"]),
            clicPlay(player) {
                //点击跳入另一个页面，并把数据push进路由里
                this.$router.push({
                    name: 'searchild',
                    params: {
                        tier: player.tier,//段位
                        queue: player.queue,//级别
                        qquin: player.qquin,//用户id
                        areaid: player.area_id//大区id
                    }
                })
            }
        },
        created() {
            //this.$parent.headerTitle = "玩家"
            this.setTitle("搜索");
            this.$parent.toubu = true;//头部显示
            this.$parent.show = true;//底部显示
            this.setCur(1);//更改玩家底部图标（变蓝）
        },
        filters: {
            getAreaName,
            headUrl
        }
}
</script>
<style lang="less">
.search {
    width: 100%;
    height: 1.27rem;
    background-color: #40a6fb;
    position: relative;
    margin-top: -0.01rem;
    input {
        width: 6.15rem;
        height: .69rem;
        position: absolute;
        left: .45rem;
        top: .4rem;
        font-size: .28rem;
        color: #787878;
        border-radius: .04rem;
        padding-left: .36rem;
    }
    .search_logo {
        width: .25rem;
        height: .23rem;
        position: absolute;
        background-image: url("../images/img/search.png");
        background-size: cover;
        background-repeat: no-repeat;
        right: 1.35rem;
        top: .65rem;
    }
    .search_font {
        position: absolute;
        color: #41a5fa;
        font-size: .22rem;
        right: .72rem;
        top: .6rem;
    }
}

.nothing {
    width: 100%;
    height: 8.69rem;
    position: absolute;
    top: 2.1rem;
    left: 0;
    img {
        width: 100%;
        height: 8.69rem;
    }
}

.playDiv {
    width: 100%;
    height: 1.64rem;
    border-bottom: .01rem solid #e1e1e1;
    position: relative;
    img {
        width: 1rem;
        height: 1rem;
        border-radius: 50%;
        position: absolute;
        left: .41rem;
        top: .46rem;
    }
    .pname {
        color: #000000;
        position: absolute;
        font-weight: bold;
        font-size: .29rem;
        left: 1.77rem;
        top: .56rem;
    }
    .area {
        color: #666666;
        position: absolute;
        font-size: .22rem;
        left: 1.77rem;
        top: 1.11rem;
    }
    .grade {
        color: #666666;
        position: absolute;
        font-size: .22rem;
        left: 3rem;
        top: 1.11rem;
    }
}
</style>



