<template>
    <div class="bigDiv">
        <div class="heroesDiv">
            <div class="left_hero" v-for="hero in championList" @click="toDetail(hero)">
                <img :src="hero.id|headUrlHeroes">
                <p class="chinaName">{{hero.title}}</p>
                <!-- 添加英雄名字和英文名字 -->
                <span class="englishName">{{hero.cname}}({{hero.ename}})</span>
            </div>
        </div>
    </div>
</template>
<script>
import {
    headUrlHeroes
} from '../filters'
import {
    mapGetters,
    mapActions
} from "vuex"
export default {
    data() {
            return {
                heroes: []
            }
        },
        methods: {
            //路由添加英雄id
            toDetail(hero) {
                this.$router.push({
                    name: 'hero',
                    params: {
                        id: hero.id
                    }
                });
            },
            //调用方法
            ...mapActions(["setTitle", "setChampionList","setCur"])
        },
        created() {
            this.setChampionList();
            this.setTitle("英雄列表");
            this.$parent.toubu=true;
            this.$parent.show=true;
            this.setCur(2);
        },
        computed: {
            //获取championList
            ...mapGetters(["championList"])
        },
        filters: {
            headUrlHeroes
        }
}
</script>
<style lang="less">
.bigDiv {
    overflow: hidden;
    width: 100%;
    .heroesDiv {
        width: 7.21rem;
        overflow: hidden;
        float: left;
        margin-left: .29rem;
        .left_hero {
            width: 3.6rem;
            height: 1.15rem;
            float: left;
            position: relative;
            margin-top: .32rem;
            margin-bottom: .03rem;
            img {
                width: 1.15rem;
                height: 1.15rem;
                position: absolute;
                left: 0;
                border-radius: 50%;
            }
            .chinaName {
                font-size: .28rem;
                font-weight: bold;
                position: absolute;
                top: .23rem;
                left: 1.29rem;
                color: #000000;
                width: 1.15rem;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
            .englishName {
                font-size: .26rem;
                font-weight: normal;
                position: absolute;
                top: .64rem;
                left: 1.29rem;
                color: #000000;
                width: 2.2rem;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
        }
    }
}
</style>



