<template>
    <div>
        <!-- 头部 -->
        <div class="header" v-if="toubu">
            <div class="return" @click="back()"></div>
            <p class="header_title">{{msg}}</p>
        </div>
        <!-- //空白div -->
        <div style="width: 100%;height: .79rem;"></div>
        <!-- 更换的中间部分 -->
        <router-view></router-view>
        <div style="height:1.1rem;width:100%;"></div>
        <!-- 底部 -->
        <div class="footer" v-if="show">
            <!-- 资讯 -->
            <router-link to='/index/News'>
                <div class="page">
                    <div :class="['news_bg',isCur==0?'blue':'gray']"></div>
                </div>
            </router-link>
            <!-- 玩家 -->
            <router-link to='/index/Players'>
                <div class="page">
                    <div :class="['player_bg',isCur==1?'blue':'gray']"></div>
                </div>
            </router-link>
            <!-- 英雄 -->
            <router-link to='/index/heroes'>
                <div class="page">
                    <div :class="['heroes_bg',isCur==2?'blue':'gray']"></div>
                </div>
            </router-link>
            <!-- 视频 -->
            <router-link to='/index/video'>
                <div class="page">
                    <div :class="['video_bg',isCur==3?'blue':'gray']"></div>
                </div>
            </router-link>
        </div>
    </div>
</template>
<script>
import {
    mapGetters
} from "vuex"
export default {
    computed: {
        ...mapGetters(["msg","isCur"])
        //test: "test"
    },
    data() {
        return {
            show: "false",
            toubu:"true",
            show:"true"
        }
    },
    methods: {
        //返回进入页面函数
        back() {
            location.href = "#/";
        }
    },

}
</script>
<style lang="less">
.header {
    width: 100%;
    height: .8rem;
    background-color: #40a6fb;
    position: fixed;
    top: 0;
    z-index: 999;
    .return {
        position: absolute;
        width: .18rem;
        height: .34rem;
        left: .33rem;
        top: .26rem;
        background-image: url("../images/img/u31.png");
        background-size: cover;
        background-repeat: no-repeat;
    }
    .header_title {
        color: #ffffff;
        font-size: .34rem;
        text-align: center;
        line-height: .8rem;
    }
}

.footer {
    width: 100%;
    height: .98rem;
    background-color: #fafafa;
    position: fixed;
    bottom: 0;
    border-top: .12rem solid #eaeaea;
    border-image: -webkit-linear-gradient( #fafafa, #eaeaea) 30 30;
    z-index: 999;
    .page {
        width: 1.875rem;
        height: .98rem;
        float: left;
        position: relative;
        a {
            display: block;
            height: 100%;
        }
        .news_bg {
            width: .42rem;
            height: .74rem;
            background-size: cover;
            background-repeat: no-repeat;
            position: absolute;
            left: .75rem;
            top: .18rem;
            &.gray {
                 background-image: url("../images/img/view.png");
            }
            &.blue {
                 background-image: url("../images/img/view-a.png");
            }
        }
        .player_bg {
            width: .42rem;
            height: .74rem;
            background-size: cover;
            background-repeat: no-repeat;
            position: absolute;
            left: .75rem;
            top: .18rem;
            &.gray {
                  background-image: url("../images/img/player.png");
            }
            &.blue {
                 background-image: url("../images/img/player-a.png");
            }
        }
        .heroes_bg {
            width: .42rem;
            height: .74rem;
            background-size: cover;
            background-repeat: no-repeat;
            position: absolute;
            left: .75rem;
            top: .18rem;
            &.gray {
                  background-image: url("../images/img/heroes.png");
            }
            &.blue {
                 background-image: url("../images/img/heroes-a.png");
            }
        }
        .video_bg {
            width: .42rem;
            height: .74rem;
            background-size: cover;
            background-repeat: no-repeat;
            position: absolute;
            left: .75rem;
            top: .18rem;
            &.gray {
                  background-image: url("../images/img/video.png");
            }
            &.blue {
                 background-image: url("../images/img/video-a.png");
            }
        }
    }
}
</style>



