<template>
    <div>
        <div class="topSpace"></div>
        <div style="overflow: hidden;">
            <div class="videoDIv" v-for="item in videoList">
                <a :href='item.url'>
                    <img v-lazy="item.img">
                    <p class="videoTitle">{{item.title}}</p>
                    <p class="videoData">{{item.title.substr(0,10)}}</p>
                    <p class="pinglun">{{getNumber(item.comments)}}</p>
                </a>
            </div>
        </div>
    </div>
</template>
<script>
//导入videolist
import {
    videoList
} from "../data/videolist"
import {
    mapActions
} from "vuex"
export default {
    data() {
            return {
                //返回videoList数据
                videoList
            }
        },
        methods: {
            //对函数进行人数评论的判断
            getNumber: function(num) {
                if (num > 10000) {
                    return (num / 10000).toFixed() + "万评论";
                } else {
                    return num + "评论";
                }
            },
            ...mapActions(["setTitle", "setCur"])
        },
        created() {
            //this.$parent.headerTitle = "最新视频"
            this.setTitle("最新视频");
            this.setCur(3)
        }
}
</script>
<style lang="less">
.topSpace {
    width: 100%;
    height: .15rem;
}

.videoDIv {
    width: 3.31rem;
    min-height: 3.6rem;
    float: left;
    margin-left: .295rem;
    position: relative;
    img {
        width: 3.31rem;
        height: 2.19rem;
        position: absolute;
        top: 0;
        left: 0;
    }
    .videoTitle {
        position: absolute;
        color: #323232;
        top: 2.36rem;
        left: 0;
        width: 3.31rem;
        font-size: .23rem;
        font-weight: bold;
    }
    .videoData {
        color: #828282;
        font-size: .18rem;
        position: absolute;
        top: 3.1rem;
        left: 0;
        text-overflow: ellipsis;
        width: 2rem;
        white-space: nowrap;
        overflow: hidden;
    }
    .pinglun {
        color: #3fa8f9;
        margin-left: 2.4rem;
        position: absolute;
        top: 3.1rem;
        right: .05rem;
        font-size: .18rem;
        text-overflow: ellipsis;
        width: 1rem;
        white-space: nowrap;
        overflow: hidden;
    }
}
</style>



